const Marketplace = () => {
  const { user } = useContext(NearContext);
  const { nftContract, getGemsSupply, getGemsForType } = useContext(NftContractContext);
  const { getSalesPopulated, marketContract, getSalesMarket, getTotalVolume, getSupplySales } = useContext(MarketContractContext);

  const [mintedNFTs, setMintedNFTs] = useState (0);
  const [ownerscnt, setOwnersCnt] = useState (0);
  const [floorprice, setFloorPrice] = useState (0);
  const [totalvolume, setTotalVolume] = useState (0);
  
  const dispatch = useDispatch();
  const rankData = useSelector((state) => state.rank);
  
  const { data, fetchNextPage, hasNextPage, isFetching } = useInfiniteQuery(
    QUERY_KEYS.SALES_POPULATED,
    ({ pageParam = 0 }) => getSalesPopulated(String(pageParam), String(APP.MAX_ITEMS_PER_PAGE_HOME)),
    {
      getNextPageParam(lastPage, pages) {
        if (lastPage.length === APP.MAX_ITEMS_PER_PAGE_HOME) {
          return pages.length * APP.MAX_ITEMS_PER_PAGE_HOME;
        }

        return undefined;
      },
      onError() {
        toast.error('Sorry  There was an error getting nauts you own.');
      },
      enabled: !!nftContract && !!marketContract,
      select: (dataRaw) => {
        if (dataRaw?.pages?.length) {
          return dataRaw.pages.flat();
        }

        return [];
      },
    }
  );

  useEffect(async ()=>{
    
    if(data){

      //get title
      let hash_data=[]
      let cx = data.length;
      for(let p=0; p < APP.MAX_ITEMS_PER_PAGE_HOME; p++){
        hash_data.push(data[cx-p-1])
      }
      //console.log('fetchData',hash_data)
      let hash=[]
      for(let i = 0; i< hash_data.length; i++){
        let cur_naut_name = hash_data[i].metadata.title
        let cur_real_name = cur_naut_name.split("#");
        hash.push(parseInt(cur_real_name[1],10));
      }

      //check
      if(rankData.data.filter(x=>hash.includes(x.num)).length ==0 ){

        let itemsJSON = await getMintNauts(APP.TOKEN_TYPE,hash);
        if(!itemsJSON) return;
        dispatch(rankActions.addRank(itemsJSON));     
      }
      console.log('state',rankData)
    }
    
  },[data]);

  useEffect(async () => {
    document.addEventListener('scroll', trackScrolling);
    //let itemsJSON = await getMintNauts(APP.TOKEN_TYPE);
    //console.log('itemsTotal',itemsJSON)
    //await getSalesMarket();

    return 
      document.removeEventListener('scroll', trackScrolling);
    

  }, [NftContractContext, MarketContractContext]);

  const isBottom = (el) => {
    if(el.getBoundingClientRect())
      return el.getBoundingClientRect().bottom <= window.innerHeight;
    else null;
  }
  const trackScrolling =async () => {
    const wrappedElement = document.getElementById('nauts-item');
    if(wrappedElement){
      if (isBottom(wrappedElement)) {
        //console.log('header bottom reached');
        fetchNextPage();
        //
      }
    }
  };

  useEffect(async() => {
    let salessupply = await getSupplySales();
    setMintedNFTs(salessupply);
    let tradevolumn = await getTotalVolume();
    setTotalVolume(convertYoctoNearsToNearsVolumn(tradevolumn));
    // let sales = await getSalesMarket();


    // let totalprice = sales?.length&& sales.reduce((x,y)=>{
    //   let data_tmp = convertYoctoNearsToNears(y.conditions.near);
    //   if (parseInt(x,10) > parseInt(data_tmp,10)) {
    //     x = data_tmp;
    //   }
    //    return x;
    // },convertYoctoNearsToNears(sales[0].conditions.near))

    // let totalitem = sales?.length&& sales.reduce((x,y)=>{
      
    //   if(x.indexOf(y.owner_id)===-1)
    //      x.push(y.owner_id)
    //    return x
    // },[])

    // setFloorPrice(totalprice||0)
    
    //setOwnersCnt(totalitem.length*5||0);

    //delete this line
    setFloorPrice(7.5)
    setOwnersCnt(1032);
    

  },[MarketContractContext])



  return (
    <Container >
      <div className="description-container" >
        <div className="desc">Marketplace</div>
          <div className="royalty">The marketplace has a 5% royalty for project maintenance</div>
          <div className="all-attributes">
            <div className = "attributes">
              <div className = "attributes-tytle">{'Sales of NFTs'}</div>
              <div className = "attributes-value">{mintedNFTs}</div>
            </div>
            {/*<div className = "attributes">
              <div className = "attributes-tytle">{'Owners'}</div>
              <div className = "attributes-value">{ownerscnt}</div>
            </div>*/}
            <div className = "attributes">
              <div className = "attributes-tytle">{'Price Floor'}</div>
              <div className = "attributes-value">{floorprice}</div>
            </div>
            <div className = "attributes">
              <div className = "attributes-tytle">{'Total Volume'}</div>
              <div className = "attributes-value">{totalvolume}</div>
            </div>
          </div>
      </div>
      <div className="items-container">
        <div className="desc">Live Nauts</div>
        <div className="items" id="nauts-item">
          {data?.length && data.map((sale) => <ArtItemPriced key={sale.token_id} nft={sale} isLink isFromIpfs rankText={rankData.data.filter(x=>x.num==parseInt(((sale.metadata.title).split("#"))[1],10))[0]?.rank }/>)}
          {!data?.length && !isFetching && (
            <div className="no-nfts">
              There is nothing here  <br />
              
            </div>
          )}
        </div>
        {hasNextPage && !isFetching && (
          <Button isPrimary onClick={() => fetchNextPage()} isDisabled={isFetching} className="load-more">
            Load more
          </Button>
        )}
        {isFetching && <Loading />}
      </div>
    </Container>
  );
}

export default Marketplace;